import React from "react";
import { Container, } from "reactstrap";
import alphavantage from './IndexSections/alphavantage.js';
import Hero from "./IndexSections/Hero.js";
import stockClosures from "./IndexSections/stockClosures.js";
import Tabs from "./IndexSections/Tabs.js";

class Index extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      amount:"",
      stock:"some stock",
      searchResults:[
                       {symbol: 'XYZ',
                        date: '2020-04-21',
                        close : 123.4,
                        day: 'Monday'},
                    ],
      monday:0,
      tuesday:0,
      wednesday:0,
      thursday:0,
      friday:0,
      control:0,
      totalYears:0,
      remainingWeeks:0,
      weeklyAmount:0,
      monthlyAmount:0,
      monthStart:0,
      monthEnd:0,
      quarterAmount:0,
      quarter:0,
      yearAmount:0,
      year:0,
      
      }
    this.search = this.search.bind(this);
    this.dayCalc = this.dayCalc.bind(this);
    this.handleAmountChange = this.handleAmountChange.bind(this)
    this.handleStockChange = this.handleStockChange.bind(this)
  }

  getInitialState(){
    return ({amount: "0.00"});
  }

  handleAmountChange(event){
    this.setState({amount:event.target.value})
  }

  handleStockChange(event){
    this.setState({stock: event.target.value.toUpperCase()})
  }

  search(){
    //creates data set of blanks from closures
    const closures = stockClosures.createBlanks()
    //api call to alphavantage
    alphavantage.search(this.state.stock).then(searchResultsNew =>  
      { 
        let minDate = searchResultsNew[searchResultsNew.length-1].date
        //combines blanks with api results
        let combined = searchResultsNew.concat(closures)
        //sorts them by date
        combined.sort((a,b) => {
          let c = new Date(a.date);
          let d = new Date(b.date)
          return c-d})
        //filters out blanks that occur in the future and in the past. slices to ensure start on monday and ends on friday
        let filtered = combined.filter(stock => {
          let today = new Date()
          let stockDate = new Date(stock.date)
          let minimumDate = new Date(minDate)
          return (stockDate < today) && (stockDate > minimumDate)})
        //adds closing price for next entry
        let final = filtered.map(stock=>{
          if(stock.symbol === 'closed')
            { let i = filtered.indexOf(stock);
              while (filtered[i].symbol === 'closed'){
                    i++
                    stock.close = filtered[i].close
                  }
            }
          return stock
          })
        ///trims data set to start on a monday and end on a friday
        let start = final.slice(0,5)
        let end = final.slice(-5,)
        let firstMondayIndex = start.map(stock => {return stock.day}).indexOf('Monday');
        let finalFridayIndex = end.map(stock => {return stock.day}).indexOf('Friday');
        let finalFinal = final.slice(firstMondayIndex, (final.length - (4 - finalFridayIndex)))
        
    this.setState({searchResults: finalFinal})
    this.dayCalc()})
    }
    dayCalc(){
      const finalClose = this.state.searchResults[this.state.searchResults.length-1].close
      const mondays = this.state.searchResults.filter(stock => {return stock.day === "Monday"})
      const mondaySum = mondays.reduce( (sum, stock) => {return sum + (this.state.amount/stock.close*finalClose)}, 0)
      const tuesdays = this.state.searchResults.filter(stock => {return stock.day === "Tuesday"})
      const tuesdaySum = tuesdays.reduce( (sum, stock) => {return sum + (this.state.amount/stock.close*finalClose)}, 0)
      const wednesdays = this.state.searchResults.filter(stock => {return stock.day === "Wednesday"})
      const wednesdaySum = wednesdays.reduce( (sum, stock) => {return sum + (this.state.amount/stock.close*finalClose)}, 0)
      const thursdays = this.state.searchResults.filter(stock => {return stock.day === "Thursday"})
      const thursdaySum = thursdays.reduce( (sum, stock) => {return sum + (this.state.amount/stock.close*finalClose)}, 0)
      const fridays = this.state.searchResults.filter(stock => {return stock.day === "Friday"})
      const totalYs = (fridays.length/52).toFixed(0) 
      const remainingWs = fridays.length%52
      const fridaySum = fridays.reduce( (sum, stock) => {return sum + (this.state.amount/stock.close*finalClose)}, 0)
      //creates array of the min day in each month
      let monthStart = []
      let monthEnd = []
      monthStart.push(this.state.searchResults[0])
      for (let i =1; i<this.state.searchResults.length;i++){
        let stock = this.state.searchResults[i]
        let prevStock = this.state.searchResults[i-1]
        if(stock.month !== prevStock.month){
          monthStart.push(stock)
          monthEnd.push(prevStock)
        }
      }
      monthEnd.push(this.state.searchResults[this.state.searchResults.length-1])
      //month calc
      let monthlyA = (fridays.length*this.state.amount)/monthStart.length
      const monthStartSum = monthStart.reduce( (sum, stock) => {return sum + (monthlyA/stock.close*finalClose)}, 0)
      const monthEndSum = monthEnd.reduce( (sum, stock) => {return sum + (monthlyA/stock.close*finalClose)}, 0)
      //quarter calc
      let quarters = monthStart.filter(stock => {return (stock.month === 0) || (stock.month === 3) || (stock.month === 6) || (stock.month ===9)})
      let quarterA = (fridays.length*this.state.amount)/quarters.length
      const quarterSum = quarters.reduce( (sum, stock) => {return sum + (quarterA/stock.close*finalClose)}, 0)
      //year calc
      let years = monthStart.filter(stock => {return stock.month === 0 })
      let yearA = (fridays.length*this.state.amount)/years.length
      const yearSum = years.reduce( (sum, stock) => {return sum + (yearA/stock.close*finalClose)}, 0)


      this.setState({control:fridays.length*this.state.amount})
      this.setState({totalYears:totalYs})
      this.setState({remainingWeeks:remainingWs})
      this.setState({monday:mondaySum})
      this.setState({tuesday:tuesdaySum})
      this.setState({wednesday:wednesdaySum})
      this.setState({thursday:thursdaySum})
      this.setState({friday:fridaySum})
      this.setState({weeklyAmount:this.state.amount})
      this.setState({monthStart: monthStartSum})
      this.setState({monthEnd: monthEndSum})
      this.setState({monthlyAmount: monthlyA})
      this.setState({quarter:quarterSum})
      this.setState({quarterAmount: quarterA})
      this.setState({year:yearSum})
      this.setState({yearAmount: yearA})
    }

    

  render() {
 
    return (
      <>
      <main ref="main">
        <Hero search ={this.search} 
              handleAmountChange = {this.handleAmountChange} 
              stock = {this.handleStockChange}
              amount = {this.state.amount} />
        
        <section className="section section-sm">
            <Container>
            <Tabs 
                totalYears = {this.state.totalYears}
                searchResults = {this.state.searchResults}
                remainingWeeks = {this.state.remainingWeeks}
                control = {this.state.control}
                weeklyAmount = {this.state.weeklyAmount}
                monthlyAmount = {this.state.monthlyAmount}
                monthStart = {this.state.monthStart}
                monthEnd = {this.state.monthEnd}
                monday = {this.state.monday}
                tuesday = {this.state.tuesday}
                wednesday = {this.state.wednesday}
                thursday = {this.state.thursday}
                friday = {this.state.friday}
                stock = {this.state.stock}
                quarter ={this.state.quarter}
                quarterAmount = {this.state.quarterAmount}
                year ={this.state.year}
                yearAmount = {this.state.yearAmount}
            />
            </Container>
          </section>
            
      </main>
     
    </>
    );
  }
}

export default Index;
