import React from "react";
// nodejs library that concatenates classes
import classnames from "classnames";

// reactstrap components
import {
  Card,
  CardBody,
  NavItem,
  NavLink,
  Nav,
  TabContent,
  TabPane,
  Row,
  Col
} from "reactstrap";

class TabsSection extends React.Component {
  state = {
    iconTabs: 1,
    plainTabs: 1
  };
  toggleNavs = (e, state, index) => {
    e.preventDefault();
    this.setState({
      [state]: index
    });
  };
  render() {

    const formatter = new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
      minimumFractionDigits: 0,
    })
    return (
      <>
        
        <Row className="justify-content-center">
            
          <Col className="mt-5 mt-lg-0" lg="10">
            {/* Menu */}
            <div className="mb-3">
            <h3 className="h4 text-success font-weight-bold mb-4">Why Dollar Cost Average?</h3>
              <small>
                     <p> Continuous investing frees an investor from the difficulty of timing the market and guarantees an average return. But how often should you invest to achieve the optimal average? Let's explore 4 scenarios.</p>
              </small>
            </div>
            <div className="nav-wrapper">
              <Nav
                className="nav-fill flex-column flex-md-row"
                id="tabs-icons-text"
                pills
                role="tablist"
              >
                <NavItem>
                  <NavLink
                    aria-selected={this.state.plainTabs === 1}
                    className={classnames("mb-sm-3 mb-md-0", {
                      active: this.state.plainTabs === 1
                    })}
                    onClick={e => this.toggleNavs(e, "plainTabs", 1)}
                    href="#pablo"
                    role="tab"
                  >
                    Weekly
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    aria-selected={this.state.plainTabs === 2}
                    className={classnames("mb-sm-3 mb-md-0", {
                      active: this.state.plainTabs === 2
                    })}
                    onClick={e => this.toggleNavs(e, "plainTabs", 2)}
                    href="#pablo"
                    role="tab"
                  >
                    Monthly
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    aria-selected={this.state.plainTabs === 3}
                    className={classnames("mb-sm-3 mb-md-0", {
                      active: this.state.plainTabs === 3
                    })}
                    onClick={e => this.toggleNavs(e, "plainTabs", 3)}
                    href="#pablo"
                    role="tab"
                  >
                    Quarterly
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    aria-selected={this.state.plainTabs === 4}
                    className={classnames("mb-sm-3 mb-md-0", {
                      active: this.state.plainTabs === 4
                    })}
                    onClick={e => this.toggleNavs(e, "plainTabs", 4)}
                    href="#pablo"
                    role="tab"
                  >
                    Yearly
                  </NavLink>
                </NavItem>
              </Nav>
            </div>
            <Card className="shadow">
              <CardBody>
                <TabContent activeTab={"plainTabs" + this.state.plainTabs}>
                  <TabPane tabId="plainTabs1">
                  <p className="description"> You invested {formatter.format(this.props.weeklyAmount)} in {this.props.stock} on a <b>weekly</b> basis for {this.props.totalYears} years and {this.props.remainingWeeks} weeks. Here are the amounts for each day of the week that the NYSE was open:</p>
                    <p className="description">Total Amount Invested: {formatter.format(this.props.control)}</p>
                    <p className="description">Investing Weekly on a Monday: {formatter.format(this.props.monday)}</p>
                    <p className="description">Investing Weekly on a Tuesday: {formatter.format(this.props.tuesday)}</p>
                    <p className="description">Investing Weekly on a Wednesday: {formatter.format(this.props.wednesday)}</p>
                    <p className="description">Investing Weekly on a Thursday: {formatter.format(this.props.thursday)}</p>
                    <p className="description">Investing Weekly on a Friday: {formatter.format(this.props.friday)}</p>    
                    
                  </TabPane>
                  <TabPane tabId="plainTabs2">
                  <p className="description"> You invested {formatter.format(this.props.monthlyAmount)} in {this.props.stock} on a <b>monthly</b> basis for {this.props.totalYears} years and {this.props.remainingWeeks} weeks. Here are the returns for the first and last days of the month that the NYSE was open:</p>
                    <p className="description">Total Amount Invested: {formatter.format(this.props.control)}</p>
                    <p className="description">First Day of the Month: {formatter.format(this.props.monthStart)}</p>
                    <p className="description">Last Day of the Month: {formatter.format(this.props.monthEnd)}</p>
                  </TabPane>
                  <TabPane tabId="plainTabs3">
                    
                    <p className="description"> You invested {formatter.format(this.props.quarterAmount)} in {this.props.stock} on a <b >quarterly </b> basis for {this.props.totalYears} years and {this.props.remainingWeeks} weeks. Here are the returns for the first day that the NYSE was open of each quarter:</p>
                    <p className="description">Total Amount Invested: {formatter.format(this.props.control)}</p>
                    <p className="description">First Day of the Quarter: {formatter.format(this.props.quarter)}</p>
                    </TabPane>
                    <TabPane tabId="plainTabs4">
                    
                    <p className="description"> You invested {formatter.format(this.props.yearAmount)} in {this.props.stock} on a yearly basis for {this.props.totalYears} years and {this.props.remainingWeeks} weeks. Here are the returns for the first day that the NYSE was open of each January :</p>
                    <p className="description">Total Amount Invested: {formatter.format(this.props.control)}</p>
                    <p className="description">First Day of the Year: {formatter.format(this.props.year)}</p>
                    </TabPane>
                </TabContent>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </>
    );
  }
}

export default TabsSection;
