
const alphavantage = { 
    getDay(dateString){
        let days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        let d = new Date(dateString);
        return days[d.getDay()];
    },
    getMonth(dateString){
        let d = new Date(dateString);
        return d.getMonth();
    },

    search(symbol){
     const apikey = "MDTO8LQSJVCOP738"
     return  fetch(`https://www.alphavantage.co/query?function=TIME_SERIES_DAILY&symbol=${symbol}&outputsize=full&apikey=${apikey}`)
        .then(response => {
            return response.json()
            })
            .then(data => {
                let objTimeSeries = data['Time Series (Daily)']
                let objMetaData = data['Meta Data']
                let stockDates = Object.keys(data['Time Series (Daily)'])
                return stockDates.map(stockDate => 
                    { let newDate = new Date(stockDate)
                      return {symbol: objMetaData['2. Symbol'],
                              date: newDate.toUTCString(),
                              day: this.getDay(stockDate),
                              month: this.getMonth(stockDate),
                              close : parseFloat(objTimeSeries[stockDate]['4. close']),
                             }
                    })
                })
        .catch(err => {
            alert('please check your stock symbol spelling');
        });
    }
 }

export default alphavantage;
    